.header {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 10;
  background-color: #fff;
  visibility: visible;
  transform: translateY(0);
  transition: transform 0.5s ease-in-out,
    box-shadow 0.5s ease-in-out;

  .logo {
    @include desktop {
      margin-right: 70px;
    }

    @include mobile {
      margin-right: auto;
    }

    @include mobile-m {
      max-width: 144px;
    }

    & img {
      @include mobile-s {
        height: 18px;
      }
    }
  }


  &__menu {
    @include flex-all-sb;
    position: absolute;
    max-width: 560px;
    left: 0;
    right: 0;
    margin: 0 auto;
    transform: translateY(0);
    visibility: visible;
    opacity: 1;
    animation: menu-open 400ms ease-in-out;

    @include desktop {
      bottom: 10px;
    }

    @include mobile-m {
      visibility: hidden;
      top: 40px;
      max-width: 320px;
      width: 100%;
      background-color: #fff;
      bottom: auto;
      display: block;
      opacity: 0;
      right: -24px;
      margin: 0;
      left: auto;
      transition: visibility 0.5s ease-in,
        opacity 0.3s ease-in-out,
        transform 0.3s ease-in-out;
      transform: translateX(100%);
      padding: 24px;
      padding-top: 40px;
      height: calc(100vh - 135px);
      overflow: scroll;
      box-shadow: 0px 20px 30px rgba(0, 0, 0, 0.05);
    }

    &--open {
      @include mobile-m {
        opacity: 1;
        visibility: visible;
        transform: translateX(0);
      }
    }

    @keyframes menu-open {
      0% {
        transform: translateY(-100%);
        visibility: hidden;
        opacity: 0;
      }

      100% {
        transform: translateY(0);
        visibility: visible;
        opacity: 1;
      }
    }
  }

  &__nav {
    @include flex-all-sb;
    padding: 35px 0;
    position: relative;
    transition: all 0.5s ease-in-out;

    @include desktop {
      justify-content: center;
      padding: 35px 0 50px;
    }

    @include mobile-m {
      padding: 15px 0;
    }

    &--active {
      padding-bottom: 50px;
    }

    &--open {

      .header__menu {
        transform: translateY(-100%);
        visibility: hidden;
        opacity: 0;
        animation: menu-close 400ms ease-in-out;

        @keyframes menu-close {
          0% {
            transform: translateY(0);
            visibility: visible;
            opacity: 1;
          }

          100% {
            transform: translateY(-100%);
            visibility: hidden;
            opacity: 0;
          }
        }
      }
    }
  }

  &__item {
    &:not(:last-child) {
      margin-right: 32px;

      @include mobile {
        margin-right: 20px;
      }

      @include mobile-m {
        margin-right: 0;
        padding: 7px 0;
        margin-bottom: 10px;
      }
    }
  }

  &__action {
    @include flex-c-e;
    max-width: 830px;
    width: calc(100% - 32%)
  }

  &__action-item {

    &--search {
      width: 100%;

      @include desktop {
        max-width: 80%;
      }

      @include mobile {
        max-width: 70%;
      }
    }

    &:not(:last-child) {
      margin-right: 16px;

      @include mobile-s {
        margin-right: 10px;
      }
    }

    &:hover {
      svg {
        stroke: var(--color-hover);
      }
    }


    &:active {
      svg {
        stroke: var(--color-hover);
      }
    }
  }

  .search {
    max-width: 140px;
    width: 100%;
    margin-left: auto;
    transition: all 0.5s ease-in;
    transform: translate(10px, -1px);

    @include mobile-m {
      max-width: 0;
    }

    &__header {
      position: relative;
    }

    &--open {
      max-width: 100%;
      transition: all 0.5s ease-in;

      .search__header {
        max-width: 450px;
        

        @include desktop {
          max-width: 320px;
        }

        @include mobile {
          max-width: 240px;
        }

        @include mobile-m {
          max-width: 100%;
        }
      }

      @include mobile-m {
        transition: all 0.8s ease-in;
        position: absolute;
        width: 80%;
        left: 0;
        right: 0;
        margin: 0 auto;
        bottom: 20px;
      }

      .search__close {
        visibility: visible;
        opacity: 1;
      }

    }

  }


  &__link {
    position: relative;
    @include hover-bl;
    font-size: 12px;

    &:hover {
      @include hover-full;
    }
  }

  &__action-icon {
    fill: transparent;
    stroke: var(--color-black);
    width: 24px;
    height: 24px;
    stroke-width: 2px;
    transform: translateY(4px);
    transition: stroke 0.3s ease-in-out;

    @include mobile-m {
      stroke-width: 1px;
    }

    @include mobile-s {
      width: 18px;
      height: 18px;
    }
  }

  .haeder-adv {
    background-color: var(--color-black);
    font: $font-medium;
    font-size: 16px;
    line-height: 1.1;
    letter-spacing: 0.23em;
    text-transform: uppercase;
    color: #ffffff;
    text-align: center;
    text-transform: uppercase;
    padding: 10px 0;

    @include mobile-m {
      font-size: 14px;
      padding: 30px 0;
    }
  }

  &.out {
    transform: translateY(-100%);
  }

  &__shadow {
    box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.05);

  }

}