.product {
  @include flex-s-sb;
  margin-top: 10px;
  margin-bottom: 100px;

  @include tablet {
    display: block;
  }

  &__gallery {
    width: 65%;
    display: flex;
    position: relative;
    margin-right: 30px;

    @include tablet {
      width: 100%;
      margin-right: 0;
      margin-bottom: 50px;
    }
  }

  .gallery-thumbs {
    width: 19%;
    margin-right: 20px;

    @include tablet {
      width: 25%;
    }

    .swiper-slide {
      cursor: pointer;
      object-fit: cover;
      position: relative;

      &::before {
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        background: rgba(0, 0, 0, 0.4);
        transition: background-color 0.3s ease-out;
      }

      &:hover {
        &::before {
          background-color: transparent;
        }
      }

      img {
        height: 100%;
      }
    }

    .swiper-slide-active {
      border: 1px solid var(--color-golden);

      &::before {
        background: none;
      }
    }
  }

  .gallery-preview {
    position: relative;
    width: calc(100% - 150px);
    max-height: 816px;

    @include tablet {
      width: 100%;
    }


    &__pagination {
      position: absolute;
      bottom: 30px;
      height: 40px;
      z-index: 10;
      width: 100%;
      margin: 0 auto;
      left: 0;
      @include flex-all-center;
      max-width: 140px;
      width: 100%;
      right: 0;

      @include mobile-m {
        bottom: 5px;
      }

      .swiper-pagination {
        position: relative;
        @include flex-all-center;
        max-width: 75px;
        width: 100%;
      }

      .swiper-pagination-bullet {
        width: 5px;
        height: 5px;

        &:not(:last-child) {
          margin-right: 8px;
        }
      }

      .swiper-pagination-bullet-active {
        width: 10px;
        height: 10px;
        background-color: var(--color-black);
      }

      .swiper-button-prev,
      .swiper-button-next {
        top: 95%;
        height: 8px;
        width: 14px;
      }

      .swiper-button-prev,
      .swiper-button-next {
        &:after {
          content: '';
          background-color: #000;
          mask-image: url('../img/sprite.svg#arrow-footer');
          background-repeat: no-repeat;
          background-size: contain;
          background-position: center;
          height: 8px;
          width: 14px;
          transition: background-color 0.3s ease-out;
        }

        &:hover:after {
          background-color: var(--color-hover);
        }

        &:focus:after {
          background-color: var(--color-hover);
        }
      }

      .swiper-button-next {
        &:after {
          transform: rotate(-90deg);
        }
      }

      .swiper-button-prev:after {
        transform: rotate(90deg);
      }
    }
  }

  .gallery-preview .swiper-slide {
    display: flex;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;



  }
}